import * as React from "react"
import { Flex, HStack, Box, Divider, Heading, Text, Avatar, IconButton, useColorMode, Icon, Link } from "@chakra-ui/react"
import { GoCircuitBoard, GoBriefcase, GoPerson } from "react-icons/go";
import { FaMoon, FaSun, FaLinkedin, FaGithub, FaFileExport, FaGlobeAmericas, FaDev, FaBars } from "react-icons/fa"
import profile from '../images/profile.png'
import AppNavItem from "./AppNavItem"

const AppSidebar = () => {

    const { colorMode, toggleColorMode } = useColorMode();
    const isDark = colorMode === "dark";
    const [navSize, changeNavSize] = React.useState('large')

    return (
        <>
            <Flex
                //bgColor='pink.200' header
                pos='fixed'
                h='30px'
                marginTop='0.5vh'
                boxShadow='0 4px 12 px 0 rgba(0, 0, 0, 0.05)'
                borderRadius="15px"
                w="100%"
                flexDir='row'
                justifyContent="space-between" >
                <Flex
                    flexDir="column"
                    p="1"
                    w="100%"
                    align="flex-end">
                    <Flex align="center">

                        <a href="https://www.linkedin.com/in/damzts/" target="_blank" rel="noopener noreferrer">
                            <IconButton
                                background="none"
                                borderRadius="10px"
                                ml={1}
                                size="sm" icon={isDark ? <FaLinkedin color="White" /> : <FaLinkedin color="Black" />}
                                _hover={{ background: 'gray.600' }}
                            
                            />
                        </a>
                        <a href="https://github.com/damzts" target="_blank" rel="noopener noreferrer">
                            <IconButton
                                borderRadius="10px"
                                ml={1}
                                size="sm" icon={isDark ? <FaGithub color="White" /> : <FaGithub color="Black" />}
                                _hover={{ background: 'gray.600' }}
                                background="none"
                            />
                        </a>
                        <IconButton
                            borderRadius="10px"
                            ml={1}
                            size="sm" icon={isDark ? <FaSun color="White" /> : <FaMoon color="Black" />}
                            _hover={{ background: 'gray.600' }}
                            bgColor={isDark ? "Black" : "White"}
                            onClick={toggleColorMode}
                        />
                    </Flex>
                </Flex>
            </Flex>


            <Flex
                //bgColor='gray.500' sidebar
                pos='fixed'
                left='2'
                h='96vh'
                marginTop='2.5vh'
                boxShadow='0 4px 12 px 0 rgba(0, 0, 0, 0.05)'
                borderRadius={(navSize === 'small' ? '15px' : '30px')}
                w={navSize === 'small' ? '50px' : '180px'}
                flexDir='column'
                justifyContent="space-between"
            >
                <Flex
                    p='5%'
                    flexDir='column'
                    alignItems={navSize === 'small' ? 'center' : 'flex-start'}
                    as='nav'
                >
                    <IconButton
                        background='none' mt={3}
                        _hover={{ background: 'gray.200' }}
                        icon={<FaBars />}
                        //onClick={() => { (navSize === "small" ? changeNavSize("large") : changeNavSize('small')) }}
                    />
                    <AppNavItem isDark={isDark} navSize={navSize} title='Home' icon={GoPerson} description='This is the description for the About Hover' />
                    <AppNavItem isDark={isDark} navSize={navSize} title='About' icon={FaGlobeAmericas} />
                    <AppNavItem isDark={isDark} navSize={navSize} title='Software' icon={FaDev} />
                    <AppNavItem isDark={isDark} navSize={navSize} title='Hardware' icon={GoCircuitBoard} />
                    <AppNavItem isDark={isDark} navSize={navSize} title='Projects' icon={GoBriefcase} />
                    <AppNavItem isDark={isDark} navSize={navSize} title='Resume' icon={FaFileExport} />
                </Flex>

                <Flex
                    p='5%'
                    flexDir='column'
                    w='100%'
                    align={navSize === 'small' ? 'center' : 'flex-start'}
                    mb={4}
                >
                    <Divider display={navSize === 'small' ? 'none' : 'flex'} />
                    <Flex mt={4} align='center'>
                        <Avatar size='sm' src={profile} />
                        <Flex flexDir='column' ml={4} display={navSize === 'small' ? 'none' : 'flex'}>
                            <Heading as='h3' size='sm'>David Torres</Heading>
                            <Text>Admin</Text>
                        </Flex>
                    </Flex>
                </Flex>
            </Flex>
        </>
    )
}

export default AppSidebar
