import * as React from "react"
import { Flex, VStack, Box, ColorModeScript } from "@chakra-ui/react"
import AppSidebar from "../components/AppSidebar"
import AppBodySection from "../components/AppBodySection"
import { StaticImage } from "gatsby-plugin-image"

const IndexPage = () => {
  return (
    <><ColorModeScript initialColorMode="light"></ColorModeScript>
      <Flex style={{ display: "grid" }}>
        <VStack  w="100%" style={{ gridArea: "1/1", }}
        >
          <AppBodySection/>
        </VStack>

        <Flex
          style={{ gridArea: "1/1", position: "relative", placeItems: "flex-start", display: "grid", }}
        >
          <AppSidebar />
        </Flex>
      </Flex></>
  )
}

export default IndexPage
export const Head = () => <title>Home Page</title>
